import { Injectable } from '@angular/core';
//import { element } from 'protractor';
import { Cart } from '../model/cart';
import { Produit } from '../shared/produits';
@Injectable({
  providedIn: 'root'
})
export class CartService {

  cart: Cart[]; // Dfinition du panier(cart) de type tableau
  cartData = {len: 0, cost: 0};

  constructor() { this.initCart();}

  // Mthode d'initialisation des donnes concernant le panier
  initCart():void{
    if(typeof(localStorage) !== "undefined"){// On vrifie d'abord si le navigateur du client supporte "localStorage"
      // Les donnes contenu dans la variable "cart" sont de type "chaine de caractre" donc on utilise  "JSON.parse" afin de recevoir un tableau
      // Rcupration des donnes
      const cart = JSON.parse(localStorage.getItem('cart'));
      const cartData = JSON.parse(localStorage.getItem('cartData'));
      // Initialisation des donnes
      this.cart = cart ? cart: []; // On vrifie si "cart" exite , si "cart" existe "?" on le sauvegarde, sinon ":" si "cart" n'existe pas on met un tableau vide
      this.cartData = cartData ? cartData: {len: 0, cost: 0};
    }
    else{
      this.cart = [];
      this.cartData = {len: 0, cost: 0};
    }
  }

  // Mthode de mise  jour des donnes du panier
  updateDataCart(){
    let len = 0;
    let cost = 0;
    this.cart.forEach( element => {
      len += element.number;
      cost += element.produit.prix * element.number;
    });
    this.cartData.len = len;
    this.cartData.cost = cost;
    // stockage des donnes du panier sur le navigateur de l'uitilisateur
    if(typeof(localStorage) !== "undefined"){// On vrifie d'abord si le navigateur du client supporte "localStorage"
      // localstorage supporte des donnes de type "chaine de caractere" d'ou l'utilisation "JSON.stringify" pour convertir le tableau "cart" en chaine de caractere
      localStorage.setItem('cart', JSON.stringify(this.cart));
      localStorage.setItem('cartData', JSON.stringify(this.cartData));
    }

  }

  // Mthode d'ajout au panier
  addProductToCard(newProduit: Produit): void {
    const verifierProduitExiste = this.cart.find(element => element.produit == newProduit); // On vrifie si le produit  ajouter au panier existait dans le panier

    if(verifierProduitExiste){
      verifierProduitExiste.number++;
    }
    else{
      const newProduitToAdd = {
        number: 1,
        produit: newProduit
      };
      this.cart.push(newProduitToAdd); // insertion du newProduitToAdd dans le panier
    }
    this.updateDataCart(); // Mise  jour des donnes du panier
  }

  // Mthode de suppression d'un produit du panier
  deleteFromCart(produitToDelete: Produit): void{
    const indexProduit = this.cart.findIndex(element => element.produit == produitToDelete);

    if(indexProduit !== -1){
      // Si le produit  supprimer existe plus d'une fois dans le panier alors on dcrmente ce nombre de fois
      if(this.cart[indexProduit].number > 1){
        this.cart[indexProduit].number--;
      }
      // Si le produit  supprimer existe une seule fois dans le panier alors on supprime le produit du panier
      else{
        this.cart.splice(indexProduit, 1); // Pour la mthode 'splice' il faut prciser le nombre de fois qu'on supprime un lment  un index du tableau
      }
    }
    this.updateDataCart();
  }

  removeElementOfCart(index: number): void{
    this.cart.splice(index, 1);
    this.updateDataCart();
  }

}
